              item.STATUS.includes(this.status[9]) &&
              this.parseDate(item.DATAPAGAMENTOTAXA) >= this.feePaymentDateStart.replace(/-/g, '') &&
              this.parseDate(item.DATAPAGAMENTOTAXA) <= this.feePaymentDateEnd.replace(/-/g, '') &&
              this.parseDate(item.DATA) >= this.registerDateStart.replace(/-/g, '') &&
              this.parseDate(item.DATA) <= this.registerDateEnd.replace(/-/g, '')
            )
            this.status.length === 0 ? this.licences = totalResult : this.licences = totalResult1.concat(totalResult2).concat(totalResult3).concat(totalResult4).concat(totalResult5).concat(totalResult6).concat(totalResult7).concat(totalResult8).concat(totalResult9).concat(totalResult10)

            // console.log(this.licences)

            const totalCost = this.licences.reduce((val, obj) => {
              if (!obj.CUSTOTAXA) {
                obj.CUSTOTAXA = 'R$ 0,00'
              }
              return (parseFloat(obj.CUSTOTAXA.replace('R$ ', '').replace('.', '').replace(',', '.')) + val)
            }, 0)

            const totalRev = this.licences.reduce((val, obj) => {
              if (!obj.VALORCOBRADO) {
                obj.VALORCOBRADO = 'R$ 0,00'
              }
              return (val + parseFloat(obj.VALORCOBRADO.replace('R$ ', '').replace('.', '').replace(',', '.')))
            }, 0)
            this.totalCost = new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(totalCost)
            this.totalRev = new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(totalRev)

            this.totalProfit = new Intl.NumberFormat('pt-BR', { style: 'currency', currency: 'BRL' }).format(totalRev.toFixed(2) - totalCost.toFixed(2))

            this.showReport = true
            this.loading = false
          })
        })
      },
      downloadPDF () {
        this.$refs.html2Pdf.generatePdf()
      },
      downloadXLSX (type, fn, dl) {
        var el = this.$refs.exportableTable
        var wb = XLSX.utils.table_to_book(el, {sheet:'Relatório'})
        this.loading3 = true
        setTimeout(() => {
          this.loading3 = false
          return dl ?
            XLSX.write(wb, {bookType: type, bookSTT: true, type: 'string'}) :
            XLSX.writeFile(wb, fn || (this.reportName + '.' +  (type || 'xlsx')))
        }, 100)
      },
    },
  }
</script>
